.real-details-container {
  background: #ffffff;
}
.profile-details-container {
  margin-bottom: 2em;
}
.friends-details {
  padding-top: 0.6em;
}
.officer,
.office:hover {
  color: #333333 !important;
  text-decoration: none !important;
}
.friends-details p,
.details-column p,
.officer p {
  margin-bottom: 0.3em;
}
.dashed-border-right {
  border-right: 1px dashed #c4c4c4;
  border-bottom: none;
}
.main-details {
  padding-bottom: 2em;
}
.slightly-bold {
  font-weight: 570;
}
.details-column .col-6 {
  padding: 0;
}
.profile-link, .profile-link:hover {
  color: inherit !important;
  text-decoration: none !important;
}
.officer-details {
  background: #f2f2f2;
  padding: 2em 1em;
}
.officer-profile-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: auto;
  margin-bottom: 1em;
  overflow: hidden;
}
.officer-profile-image {
  width: 100%;
  height: 100%;
}
.officer {
  text-align: center;
  margin-bottom: 1em;
}
.button-dropdown {
  display: none;
  left: -100px;
  right: 0;
  z-index: 9999999;
  background: #ffffff;
  color: #333333;
  border-radius: 4px;
  box-shadow: 1px 2px 1px 0px rgba(0,0,0,0.3);
}
.button-dropdown.show {
  display: block;
}
.button-dropdown-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.button-dropdown-list-button {
  background: transparent;
  padding: 0.25em 7em 0.25em 0;
  margin: 0;
  text-align: left;
  border: none;
}
.button-dropdown-list-button:hover {
  background-color: #00b0f0;
  color: #ffffff;
  text-align: left;
}
/*[data-placement] {
  top: -10px !important;
  left: -140px !important;
}*/
@media screen and (max-width: 768px) {
  .dashed-border-right {
    border-right: none;
    border-bottom: 1px dashed #c4c4c4;
  }
  .details-column {
    text-align: center;
  }
}
.rounded-small {
  border-radius: 8px;
}
.upload-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 996;
}
.modal-main {
  position: fixed;
  background: #ffffff;
  max-width: 90%;
  min-width: 50%;
  height: 400px;
  top: 50%;
  left: 50%;
  padding: 3em;
  border-radius: 4px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
}
.opa-0 {
  opacity: 0;
}
.opa-1 {
  opacity: 1;
}
.btn-uplaod-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin: 1em;
  height: 10em;
  width: 10em;
  overflow: hidden;
}
.btn-upload {
  border: 2px dashed #00b0f0;
  color: #00b0f0;
  background-color: white;
  padding: 8px 20px;
  border-radius: 50%;
  font-size: 20px;
  font-weight: bold;
  height: 10em;
  width: 10em;
}
.btn-upload-wrapper input[type="file"] {
  font-size: 2.2em;
  text-align: center;
  line-height: 7em;
  position: absolute;
  /* margin: auto; */
  left: 0;
  top: 0;
  opacity: 0;
}
.btn-upload-wrapper input[type="file"]:hover {
  cursor: pointer;
}
.btn-upload-wrapper input[type="file"]:hover ~ .btn-upload,
.btn-upload:hover {
  background-color: #00b0f0;
  color: #ffffff;
  transition: all 0.2s ease-in-out;
}

@media (max-width: 600px) {
.lifted-profile-image-wrapper.edit {
	margin-top: -80px !important;
	margin-right: auto;
	margin-left: auto;
}
.floating-edit-button-wrapper.\--profile-picture {
    top: 80%;
    left: 60%;
}
}

.gclout-modal-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  opacity: 1;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-animation: show 0.5s ease;
          animation: show 0.5s ease;
}

@-webkit-keyframes show {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: flex;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: flex;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.gclout-modal {
  width: 100%;
  margin: auto;
}
.gclout-modal-content {
  /* background: #ffffff; */
  padding: 0;
}

.gclout-modal-close-btn {
  position: absolute;
  top: 2em;
  right: 2em;
  width: 2rem;
  height: 2rem;
  padding: 0;
  border: 0;
  cursor: pointer;
  outline: 0;
  z-index: 999999999999999;
  background: transparent;
}
.gclout-modal-close-btn:before,
.gclout-modal-close-btn:after {
  content: "";
  position: absolute;
  top: 1rem;
  left: 0.25rem;
  width: 1.4rem;
  height: 0.1rem;
  background-color: #FFFFFF;
}
.gclout-modal-close-btn:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.gclout-modal-close-btn:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.gclout-modal-close-btn:hover:after,
.gclout-modal-close-btn:hover:before {
  background-color: #00b0f0;
}


